@import "../config/vars";

.btn {
    background-color: var(--color-black);
    border-radius: 5px;
    border: 1px solid var(--color-black);
    padding: 0.5rem;
    margin: 0 0.5rem;
    font-size: 14px;
    font-weight: bold;
    color: var(--color-white);
    @media (min-width: 425px) {
        font-size: 13px;
        padding: 0.5rem 1rem;
    }
    @media (min-width: 1024px) {
        font-size: 16px;
        padding: 0.5rem 1rem;
    }
    &__login {
        border: 1px solid var(--color-orange);
    }
    &__register {
        color: var(--color-black);
        background-color: var(--color-orange);
    }
    &__registered {
        padding: 1rem 4rem;
        font-weight: bold;
        font-size: 22px;
        color: var(--color-black);
        background-color: var(--color-orange);
        &:hover{
            text-decoration: none;
            color: var(--color-black);
            background-color: var(--color-white);
        }
    }
    &__banca{
        text-align: center;
        background-color: var(--color-dark);
        border: 1px solid var(--color-gray-lith);
    }
}